<template>
  <div style="margin-top:15px;font-size:0.8rem;-position:fixed;-top:50px;-bottom:0px;-left:0px;-right:0px;-overflow-y:scroll;">
    <div>
      <div class="live_p">
        <p>生活缴费</p>
        <img
          src="../../assets/two/add.png"
          alt
          @click="openLife('add')"
        />
      </div>
    </div>
    <!-- 开始 -->
    <table
      border-spacing="0"
      border-collapse="collapse"
      cellspacing="0"
      cellpadding="0"
      id="plice_table"
    >
      <thead>
        <tr
          v-for="(item,i) in localInfo"
          :key="i"
        >
          <th
            :style="{'width':computerWidth}"
            class="center"
          >{{ item }}</th>
        </tr>
      </thead>
      <tbody v-if="choosedLifeCostAll.length > 0 && payOldAll.length > 0">
        <tr
          v-for="(item,i) in choosedLifeCostAll"
          :key="i"
          @click.stop="openLife('edit',item)"
        >
          <!-- 费用项目 -->
          <td
            @click.stop="removeLife(item)"
            :style="{'width':computerWidth}"
            style="display:flex;align-items:center;justify-content:center;"
          >
            <img
              src="../../assets/two/remove.png"
              style="padding-right:8px;"
            />
            {{item.dictLabel}}
          </td>
          <!-- 收费方式 -->
          <td
            v-if="payOldAll.length > 0"
            :style="{'width':computerWidth}"
          >
            {{ payAllobj[item.dictMode || '2'] }}
          </td>
          <!-- 价格 -->
          <td
            :style="{'width':computerWidth}"
            class="left"
            style="color:#ff7d41;"
          >{{ item.dictPrice }}</td>
          <!-- 初始读数 -->
          <td
            v-if="item.dictValue=='waterPrice' && item.dictMode == '1' ||item.dictValue=='gasPrice' && item.dictMode == '1' ||item.dictValue=='elecPrice' && item.dictMode == '1' && elecPayModel ==0"
            :style="{'width':computerWidth}"
            class="left"
            @click.stop=""
          >
            <input
              class="input_box"
              type="number"
              step="1"
              min="0"
              @keyup="priceTwoNum(i)"
              placeholder="请输入"
              v-model="item.first"
            />
          </td>
          <!-- 初始读数 -->
          <td
            v-else
            :style="{'width':computerWidth}"
            class="left"
            @click.stop=""
          >
            <input
              class="input_box"
              type="text"
              placeholder="暂无"
              readonly
              v-model="item.first"
            />
          </td>
        </tr>
      </tbody>
    </table>
    <!-- 完成办理 -->
    <mt-button
      class="button_style"
      @click="btn_ok"
    >完成办理</mt-button>
    <!-- 添加 -->
    <!-- 展示-生活费用 -->
    <van-dialog
      v-model="show_lifeCostBox"
      :before-close="confirmDepart"
      :title="lifeType == 'edit'?'编辑费用':'添加费用'+lifeCost_mode"
      show-cancel-button
    >
      <div>
        <van-cell
          style="margin-top:10px;border-bottom: 1px solid #e4e4e4;"
          title="费用项目"
          is-link
          :disabled="lifeType == 'edit'?true:false"
          @click="lifeType == 'add'?show_lifeCostPro = true:''"
          :value="lifeCost_label"
        />
        <van-cell
          title="收费方式"
          is-link
          style="border-bottom: 1px solid #e4e4e4;"
          v-if="lifeCost_value == 'waterPrice' 
                  || lifeCost_value == 'elecPrice' 
                  || lifeCost_value == 'gasPrice' "
          @click="show_lifeCostPay = true"
          :value="lifeCost_mode"
        />
        <van-cell
          title="收费方式"
          is-link
          style="border-bottom: 1px solid #e4e4e4;"
          v-else
          value="固定收取"
        />
        <div class="unit">
          <van-field
            v-model="lifeCost_price"
            style="width:90%"
            label="单价"
          />
          <div
            style="width:20%;"
            v-if="lifeCost_value == 'waterPrice'"
          >{{ lifeCost_mode == '固定收取'?'元/月':'元/吨'}}</div>
          <div
            style="width:20%;"
            v-else-if="lifeCost_value == 'elecPrice'"
          >{{ lifeCost_mode == '固定收取'?'元/月':'元/度'}}</div>
          <div
            style="width:20%;"
            v-else-if="lifeCost_value == 'gasPrice'"
          >{{ lifeCost_mode == '固定收取'?'元/月':'元/m³'}}</div>
          <div
            style="width:20%;"
            v-else-if="lifeCost_value == 'propPrice'"
          >元/月</div>
          <div
            style="width:20%;"
            v-else-if="lifeCost_value == 'rubbishPrice'"
          >元/月</div>
          <div
            style="width:20%;"
            v-else-if="lifeCost_value == 'parkPrice'"
          >元/月</div>
          <div
            style="width:20%;"
            v-else-if="lifeCost_value == 'broadBandPrice'"
          >元/月</div>
        </div>
      </div>
    </van-dialog>
    <!-- 选择-费用项目 -->
    <van-popup
      v-model="show_lifeCostPro"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="lifeCostAll"
        @confirm="onConfirmCostType"
        @cancel="show_lifeCostPro = false"
      />
    </van-popup>
    <!-- 选择-收费方式 -->
    <van-popup
      v-model="show_lifeCostPay"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="payAll"
        @confirm="onConfirmCostPayType"
        @cancel="show_lifeCostPay = false"
      />
    </van-popup>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
import { PostBusinessOrderroomRoomin } from '@/api/api_business_hotel_and_scale';
import { GetSystemDictDataByType } from '@/api/api_query';
export default {
    data() {
        return {
            ele_price: '',
            bothInfo: [
                { name: '后付费', statu: true },
                { name: '预付款', statu: false }
            ],
            localInfo: ['费用项目', '收费方式', '价格', '初始读数'],
            add_box: false,
            show_lifeCostBox: false,
            show_lifeCostPro: false,
            show_lifeCostPay: false,
            choosedLifeCostAll: [],
            lastCost: [],
            payOldAll: [],
            payAll: [],
            lifeCostAll: [],
            payAllobj: {},
            crrent_info: '',
            elecPayModel: '',
            lifeCost_label: null,
            lifeCost_value: null,
            lifeCost_price: null,
            lifeCost_mode: null,
            lifeType: null,
            unitList: {
                1: '元/度',
                2: '元/吨',
                3: '元/m³',
                4: '元/月',
                5: '元/月',
                6: '元/月',
                7: '元/月'
            }
        };
    },
    computed: {
        computerWidth() {
            return window.innerWidth / 4 + 'px';
        }
    },
    created() {
        this.crrent_info = JSON.parse(localStorage.getItem('live_next'));
        this.lifeCostObj = JSON.parse(this.crrent_info.lifeCost);
        this.$store.commit('setHeader', '费用设置');
        this.getMore();
    },
    methods: {
        getMore() {
            GetSystemDictDataByType({ dictType: 'tb_business_life_cost_mode' }).done((res) => {
                this.payOldAll = res.data;
                this.payAll = res.data.map((item) => item.dictLabel);
                this.payAllobj = {};
                res.data.map((item) => {
                    this.payAllobj[item.dictValue] = item.dictLabel;
                });
                this.lifeCost_mode = this.payOldAll[0].dictLabel;
            });
            GetSystemDictDataByType({ dictType: 'business_life_cost' }).done((res) => {
                res.data.map((item) => {
                    item.dictPrice = null;
                    item.dictMode = null;
                });
                let short_arr = [];
                for (const k in this.lifeCostObj) {
                    let obj = {};
                    if (k == 'elecPrice' || k == 'waterPrice' || k == 'gasPrice') {
                        obj.dictValue = k;
                        obj.dictPrice = this.lifeCostObj[k].price;
                        obj.dictMode = this.lifeCostObj[k].mode;
                        short_arr.push(obj);
                    } else {
                        obj.dictValue = k;
                        obj.dictPrice = this.lifeCostObj[k];
                        obj.dictMode = '';
                        short_arr.push(obj);
                    }
                }
                short_arr.forEach((item) => {
                    res.data.forEach((item_) => {
                        if (item.dictValue == item_.dictValue) {
                            item_['dictPrice'] = item.dictPrice;
                            item_['dictMode'] = item.dictMode;
                        }
                    });
                });
                this.lifeCostOldAll = res.data;
                this.lifeCostAll = res.data.map((item) => item.dictLabel);
                this.lifeCost_label = this.lifeCostOldAll[0].dictLabel;
                this.lifeCost_value = this.lifeCostOldAll[0].dictValue;
                this.choosedLifeCostAll = res.data.filter((item) => item.dictPrice > 0);
            });
        },
        // 电费方式
        eleStyle(i) {
            this.elecPayModel = i;
            if (i == 0) {
                this.bothInfo[0].statu = true;
                this.bothInfo[1].statu = false;
            } else {
                this.bothInfo[0].statu = false;
                this.bothInfo[1].statu = true;
            }
        },
        // 删除
        removeLife(item) {
            this.choosedLifeCostAll = this.choosedLifeCostAll.filter((itm) => itm.dictValue != item.dictValue);
            this.lifeCostAll.push(item.label);
        },
        priceTwoNum(index) {
            for (let i = 0; i < this.choosedLifeCostAll.length; i++) {
                let ele = this.choosedLifeCostAll[i];
                if (index == i && ele.first) {
                    ele.first = ele.first.toString();
                    ele.first = ele.first.replace(/[^\d.]/g, ''); //清除“数字”和“.”以外的字符
                    ele.first = ele.first.replace(/\.{2,}/g, '.'); //只保留第一个. 清除多余的
                    ele.first = ele.first.replace('.', '$#$').replace(/\./g, '').replace('$#$', '.');
                    ele.first = ele.first.replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3'); //只能输入两个小数
                    if (ele.first.indexOf('.') < 0 && ele.first != '') {
                        //以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
                        ele.first = parseFloat(ele.first);
                    }
                }
            }
        },
        // 展开-生活费用
        openLife(type, item) {
            this.lifeType = type;
            let lastCost = [...this.lifeCostOldAll].filter((x) => [...this.choosedLifeCostAll].every((y) => y.dictLabel != x.dictLabel));
            if (this.lifeType == 'add') {
                this.lifeCost_price = '';
                if (lastCost.length == 0) {
                    Toast({
                        message: '暂无可添加',
                        duration: 900
                    });
                    return false;
                } else {
                    for (var i = 0; i < lastCost.length; i++) {
                        lastCost[i].dictPrice = '';
                    }
                    this.lifeCostAll = lastCost.map((item) => item.dictLabel);
                    this.lifeCost_label = this.lifeCostAll[0];
                    this.lifeCost_mode = this.payAll[0];
                    this.lifeCost_value = this.lifeCostOldAll.find((item) => item.dictLabel == this.lifeCostAll[0]).dictValue;
                    this.show_lifeCostBox = true;
                }
            } else {
                this.lifeCost_label = item.dictLabel;
                this.lifeCost_value = item.dictValue;
                this.lifeCost_price = item.dictPrice;
                this.lifeCost_mode = this.payOldAll.find((itm) => itm.dictValue == item.dictMode).dictLabel;
                this.show_lifeCostBox = true;
            }
        },
        // 选择-生活费用
        onConfirmCostType(e) {
            this.lifeCost_label = e;
            this.lifeCost_value = this.lifeCostOldAll.find((item) => item.dictLabel == e).dictValue;
            this.show_lifeCostPro = false;
        },
        // 选择-收费方式
        onConfirmCostPayType(e) {
            this.lifeCost_mode = e;
            this.show_lifeCostPay = false;
        },
        confirmDepart(action, done) {
            if (action == 'confirm') {
                done(false);
                if (!this.lifeCost_price) {
                    Toast('请输入单价');
                    return;
                }
                let dataFormCost = {
                    dictLabel: this.lifeCost_label,
                    dictValue: this.lifeCost_value,
                    dictMode: this.lifeCost_mode,
                    dictPrice: this.lifeCost_price
                };
                if (dataFormCost.dictValue == 'waterPrice' || dataFormCost.dictValue == 'elecPrice' || dataFormCost.dictValue == 'gasPrice') {
                    dataFormCost.dictMode = this.payOldAll.find((itm) => itm.dictLabel == dataFormCost.dictMode).dictValue;
                } else {
                    dataFormCost.dictMode = '2';
                }
                if (this.lifeType == 'add') {
                } else {
                    this.choosedLifeCostAll = this.choosedLifeCostAll.filter((item) => item.dictLabel != dataFormCost.dictLabel);
                }
                this.choosedLifeCostAll.push(dataFormCost);
                this.show_lifeCostBox = false;
            } else if (action == 'cancel') {
                done(false);
                this.show_lifeCostBox = false;
            }
        },
        // 完成办理
        btn_ok() {
            let price_totel = {};
            let mode_totel = {};
            let first_totel = {};
            for (var i = 0; i < this.choosedLifeCostAll.length; i++) {
                let item = this.choosedLifeCostAll[i];
                price_totel[item.dictValue] = item.dictPrice;
                mode_totel[item.dictValue] = item.dictMode;
                first_totel[item.dictValue] = item.first;
            }
            this.price_totel = price_totel;
            this.mode_totel = mode_totel;
            this.first_totel = first_totel;
            let orderRoomId = this.crrent_info.orderRoomId;
            let data = {
                orderId: this.crrent_info.orderId,
                roomUser: this.crrent_info.roomUser,
                phone: this.crrent_info.phone,
                idCard: this.crrent_info.idCard,
                roomId: this.crrent_info.roomId,
                elecPayModel: !this.elecPayModel ? 0 : this.elecPayModel,

                elecPrice: this.price_totel.elecPrice,
                waterPrice: this.price_totel.waterPrice,
                gasPrice: this.price_totel.gasPrice,
                propPrice: this.price_totel.propPrice,
                rubbishPrice: this.price_totel.rubbishPrice,
                parkPrice: this.price_totel.parkPrice,
                broadBandPrice: this.price_totel.broadBandPrice,

                elecMode: this.mode_totel.elecPrice ? this.mode_totel.elecPrice : '',
                waterMode: this.mode_totel.waterPrice ? this.mode_totel.waterPrice : '',
                gasMode: this.mode_totel.gasPrice ? this.mode_totel.gasPrice : '',

                preElecDegree: this.first_totel.elecPrice,
                preWaterDegree: this.first_totel.waterPrice,
                preGasDegree: this.first_totel.gasPrice
            };
            PostBusinessOrderroomRoomin(orderRoomId, data).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 1000
                });
                setTimeout('window.history.go(-2)', 1000);
            });
        }
    },
    watch: {}
};
</script>
<style scoped>
.c_88 {
    color: #888;
}
.life_box {
    font-size: 12px;
    color: #9b9b9b;
    text-align: left;
    padding-left: 20px;
    border-bottom: 1px solid #ebebeb;
    line-height: 30px;
    background-color: #f6f6f6;
    padding-top: 10px;
}
.rent_right {
    display: flex;
    align-items: center;
    font-size: 0.6rem;
}
.rent_right > img {
    width: 16px;
    height: 16px;
    padding-right: 4px;
}
.live_p {
    display: flex;
    justify-content: space-between;
    text-align: left;
    padding: 14px 20px;
    background: #fff;
}
.local_info {
    display: flex;
    padding: 14px 20px;
    justify-content: space-between;
    border: 1px solid red;
}
.info_box {
    background: #fff;
    border-bottom: 1px solid #e5e5e5;
    padding: 14px 20px;
}
.info_box > tr {
    display: flex;
}
.info_box > tr > td:nth-child(1) {
    display: flex;
}
.info_box > tr > td:nth-child(1) > div > img {
    padding-right: 4px;
}
.info_box > tr > td:nth-child(3) {
    text-align: right;
}
.input_box {
    color: #ff7d41;
    width: 50%;
    border: none;
    outline-style: none;
}
.button_style {
    width: 100%;
    background: #ff7d41;
    opacity: 0.9;
    color: #fff;
    font-size: 0.8rem;
    margin: 20px 0;
    height: 50px;
    position: fixed;
    left: 0;
    bottom: 0;
}
/*  */
#plice_table {
    padding-top: 10px;
    font-size: 0.6rem;
    background: #fff;
    table-layout: fixed;
    word-break: break-all;
    width: 500px;
    line-height: 50px;
}
#plice_table thead {
    color: #000;
    border-top: 2px solid #393939;
    line-height: 40px;
    display: block;
    text-align: center;
    display: flex;
}
#plice_table tbody tr td {
    border-width: 0px 1px 1px 0px;
    box-sizing: border-box;
    padding: 2px;
    text-align: center;
}
.left {
    text-align: left;
}
.center {
    border: solid #ebebeb;
    border-width: 0px 1px 1px 0px;
    box-sizing: border-box;
    text-align: center;
}
table tbody {
    overflow-y: scroll;
    position: fixed;
    height: calc(100% - 190px);
    background: #fff;
}
.unit {
    display: flex;
    background: #fff;
    font-size: 14px;
    align-items: center;
    padding-right: 16px;
}
/deep/ .van-cell__title {
    text-align: left;
}
/deep/ .van-cell__value {
    color: #000;
}
/deep/ .van-field__label {
    color: #000;
}
</style>



